import React from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import Divider from '@material-ui/core/Divider';
import {sortableContainer, sortableElement} from 'react-sortable-hoc';
import arrayMove from 'array-move';
import Paper from '@material-ui/core/Paper';
import CodeView from './codeView';

const SortableItem = sortableElement(({item}) => 
    <li style={{margin:10}}>
        <Paper style={{width:'100%',height:40,backgroundColor:'#3F51B5',display:'flex',alignItems:'center',justifyContent:'center'}}>
           <p className="priority">{item} λ² μΈν„°λ½νΈ</p>
        </Paper>
    </li>
    );

const SortableContainer = sortableContainer(({children}) => {
  return <ul style={{listStyle:'none'}}>{children}</ul>;
});

export default function SetPriority(props) {

const {priority,setPriority,PriorityDialog,setPriorityDialog} = props;

const  onSortEnd = ({oldIndex, newIndex}) => {
    setPriority(arrayMove(priority, oldIndex, newIndex));
  };
  return (
    <div align="center">
     
      <Dialog open={PriorityDialog}>
        <DialogContent>
            <br/>
            <br/>
            <CodeView codeimg="cpu.png"/>
            <p>μ„λ΅κ°μλ΅ μ°μ„ μμ„κ°€ λ†’μ•„μ§‘λ‹λ‹¤</p>
            <p>λ°•μ¤λ¥Ό swap ν•μ—¬ μ°μ„ μμ„λ¥Ό λ°”κΏ”λ³΄μ„Έμ”</p>
            <Divider />
            <SortableContainer onSortEnd={onSortEnd}>
                {priority.map((item, index) => (
                <SortableItem key={`item-${item.value}`} index={index} item={item.value} />
                ))}
            </SortableContainer>
        </DialogContent>
        <DialogActions>
          <Button onClick={()=>{setPriorityDialog(false)}} color="primary" autoFocus>
            ok
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  )}