import React from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import CodeIcon from '@material-ui/icons/Code';
import IconButton from '@material-ui/core/IconButton';
import Tooltip from '@material-ui/core/Tooltip';

export default function CodeView(props) {
  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };
  const handleOnDragStart = (e) => e.preventDefault()
  return (
    <div>
      <div align="right" style={{marginTop:-50}}>
        <Tooltip title="Code View" placement="left">
           <IconButton onClick={handleClickOpen}  aria-label="settings">
            <CodeIcon style={{color:'#3F51B5'}}/>
          </IconButton>
         </Tooltip>
        </div>
      <Dialog
        fullWidth={true}
        maxWidth="md"
        open={open}
        onClose={handleClose}
      >
          <div align="center">
              <img style={{width:'80%',height:'80%'}} src={props.codeimg} onDragStart={handleOnDragStart} />
          </div>
        <DialogActions>
          <Button onClick={handleClose} color="primary" autoFocus>
            Close
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}