import React from 'react';
import Button from '@material-ui/core/Button';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import SettingsIcon from '@material-ui/icons/Settings';

export default function SettingMenu(props) {
    const [anchorEl, setAnchorEl] = React.useState(null);
    const {setInterruptDialog, setPriorityDialog, setMaskDialog}=props;
    const handleClick = (event) => {
      setAnchorEl(event.currentTarget);
    };
  
    const handleClose = () => {
      setAnchorEl(null);
    };
  
    return (
      <div>
        <Button 
          color="primary" 
          onClick={handleClick}
        >
          <SettingsIcon/>
        </Button>

        <Menu
          id="simple-menu"
          anchorEl={anchorEl}
          keepMounted
          open={Boolean(anchorEl)}
          onClose={handleClose}
        >
          <MenuItem onClick={()=>{setInterruptDialog(true);handleClose()}}>Add interrupt</MenuItem>
          <MenuItem onClick={()=>{setPriorityDialog(true);handleClose()}}>Set Priority</MenuItem>
          <MenuItem onClick={()=>{setMaskDialog(true);handleClose()}}>Set Mask</MenuItem>
        </Menu>
      </div>
    );
  }
