import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import SimpleCard from './Card';
import {ALL_STATE_VALS} from '../_allStates';
import SelectInterrupt from './selectIntterrupt';
import ArrowRightIcon from '@material-ui/icons/ArrowRight';
import ArrowLeftIcon from '@material-ui/icons/ArrowLeft';
import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown';
import Grid from '@material-ui/core/Grid';
import Divider from '@material-ui/core/Divider';
import ViewIDT from './IDT_dialog'

let height = window.innerHeight;
let tt = true;//λ°λ³µλ°©μ§€ λ³€μ λ‚μ¤‘μ— μ§€μ°λ”κ±°

const useStyles = makeStyles({ // κ°€λ΅,μ„Έλ΅ μ¤‘μ•™μΌλ΅ μ •λ ¬ν•΄μ¤λ‹λ‹¤.
  page:{
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    width:"95%",
    height:height*0.8
  },
});

export default function InterruptRequest(props) {
  const classes = useStyles();
  const {count,interrupt,setInterrupt,plusCount,working,setWorking,mask,setCount,priority} = props // μΈν„°λ½νΈ λ‹¨κ³„ μ¤‘ ν„μ¬ λ‡λ²μ§Έ λ‹¨κ³„μΈμ§€
  const states =  ALL_STATE_VALS(working); // κ° μΈν„°λ½νΈ λ‹¨κ³„λ³„ μ„¤λ…κ³Ό κµ¬μ„±μ”μ† data

  const lightOn=(n)=>{ 
    // κ° κµ¬μ„±μ”μ†κ°€ λ‡λ²μ§Έ λ‹¨κ³„μ—μ„ μ‘λ™ν•λ”μ§€ property(array)λ΅ λ°›μµλ‹λ‹¤. 
    // μλ¥Όλ“¤μ–΄, lightOn([1,2,3])μ κ²½μ° μΈν„°λ½νΈμ 1,2,3λ²μ§Έ λ‹¨κ³„μ—μ„ μ‘λ™ν•λ” κµ¬μ„±μ”μ†λΌλ” μλ―Έμ…λ‹λ‹¤.
    
    return n.some(function(item, index) { return item === count; }) //countλ” ν„μ¬ νμ΄μ§€
    // someλ³€μλ” λ°°μ—΄μ•μ— μ–΄λ–¤ μ”μ†λΌλ„ νλ³„ ν•¨μλ¥Ό ν†µκ³Όν•λ©΄ trueλ¥Ό λ°ν™ν•©λ‹λ‹¤
  }

  const Iteration=(arr)=>{ 
    let returnValue = [0,0,0,0,0,0,0,0];
    if(Array.isArray(arr)){ 
      for(let i =0;i<arr.length;i++){
        returnValue[arr[i]]=1;
       }
    }else{ //ISRμ€ λ°°μ—΄μ΄ μ•„λ‹κΈ°λ•λ¬Έμ—
      returnValue[working]=1;
    }
    return returnValue.map((int,i)=>int===1?<b>1</b>:0)
  }// μλ¥Ό λ“¤μ–΄ λ°°μ—΄ arrμ— 3,5κ°€ μλ‹¤λ©΄ 3λ²μ§Έ 5λ²μ§Έ λΉ„νΈλ¥Ό 1λ΅ λ°”κΎΈκ³  μ¶λ ¥ 00000000 => 00010100
  

  if(lightOn([5])&&tt){
    let interrupt_clone = [...interrupt];
    interrupt_clone.sort((a, b) => a.value > b.value ? 1 : -1); // μ°μ„ μμ„ κΈ°λ° μ •λ ¬
    let will_working = interrupt_clone.pop(); // κ°€μ¥ μ°μ„ μμ„κ°€ λ†’μ€ κ²ƒμ„ pop 
    setWorking(will_working); // pop ν• μ”μ†λ¥Ό μ§„ν–‰μ¤‘μΌλ΅ set
    tt=false; // λ°λ³µλ°©μ§€ λ³€μ
    setInterrupt(interrupt_clone); // μ°μ„ μμ„κ°€ κ°€μ¥λ†’μ€κ²ƒμ΄ popλ λ°°μ—΄μΌλ΅ μ—…λ°μ΄νΈ
  }//μ°μ„ μμ„ λ†’μ€ μΈν„°λ½νΈ μ‹¤ν–‰
  
  if(lightOn([10]))tt=true;

 
    return (
      <div className={classes.page}>
      <SelectInterrupt count={lightOn([0])} interrupt={interrupt} mask={mask} setInterrupt={setInterrupt} plusCount={plusCount}/>
      <ViewIDT working={working} plusCount={props.plusCount} count={lightOn([8])}/>
      <Grid justify="center" alignItems="center" container spacing={3}> 
      {/* 3 x 3 κ·Έλ¦¬λ“ κµ¬μ΅°  */}

        <Grid justify="center" alignItems="center" item xs={9} sm={4}>
          <div align="center">
           {
              lightOn([1])?
              <img width={100} height={100} src={"hardware2.png"} alt="hardware"/>:
              <img width={100} height={100} src={"hardware.png"} alt="hardware"/>
            }
          </div>
        </Grid>
        <Grid justify="center" alignItems="center" item xs={9} sm={1}/>

        <Grid justify="center" alignItems="center" item xs={9} sm={4}>
          <SimpleCard on={lightOn([6,10])} title={"PCB"} description={
              <div>
                <p>Program Counter : <b>{states[count].pcb.pc}</b></p>
                <Divider />{/* κµ¬λ¶„μ„  */}
                <p>Context : <b>{states[count].pcb.context}</b></p>
              </div>} codeimg="pcb.png"/>
        </Grid>

        <Grid justify="center" alignItems="center" item xs={9} sm={4}>
          <div align="center">
          {
            lightOn([1])?
            <ArrowDropDownIcon fontSize="large" color="primary"/>:
            <ArrowDropDownIcon fontSize="large" color="disabled"/>
          }
          </div>
        </Grid>

        <Grid justify="center" alignItems="center" item xs={9} sm={1}/>

        <Grid justify="center" alignItems="center" item xs={9} sm={4}>
        </Grid>        

        <Grid justify="center" alignItems="center" item xs={9} sm={4}>
          <SimpleCard on={lightOn([1,2,4,5])} title={"APIC"} description={
          <div>
            <p>IRR : {Iteration(interrupt)}</p> 
            <Divider />{/* κµ¬λ¶„μ„  */}
            <p>ISR : {Iteration(working)}</p>
            <Divider />{/* κµ¬λ¶„μ„  */}
            <p>IMR : {Iteration(mask)}</p>
          </div>} codeimg="apic.png"/>
        </Grid>

        <Grid justify="center" alignItems="center" item xs={9} sm={1}>
        <div align="center">
          {
            lightOn([3])?
            <ArrowLeftIcon fontSize="large" color="primary"/>:
            <ArrowRightIcon fontSize="large" color={lightOn([2,5])?"primary":"disabled"}/>
          }
          </div>
        </Grid>

        <Grid justify="center" alignItems="center" item xs={9} sm={4}>
          <SimpleCard on={lightOn([2,3,5,6,8,9,10])} title={"CPU"} description={
          <div>
            <p>Program Counter : <b>{states[count].cpu.pc}</b></p>
            <Divider />{/* κµ¬λ¶„μ„  */}
            <p>Context : <b>{states[count].cpu.context}</b></p>
            <Divider />{/* κµ¬λ¶„μ„  */}
            <p>IEflag : <b>{states[count].cpu.ieflag}</b></p>
          </div>} codeimg="cpu.png"/>
        </Grid>

      </Grid>
    </div>
    );
  }

