import React from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import { makeStyles } from '@material-ui/core/styles';
import Divider from '@material-ui/core/Divider';

const useStyles = makeStyles({
  tablerow:{
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    width:100,
    height:50
  },
});

export default function ViewIDT(props) {

const topen = props.count;

  return (
    <div align="center">
     
      <Dialog
        open={topen}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <div align="center" style={{width:300}}>
            <p>Interrupt Description Table</p>
        </div>
        <DialogContent>
           <Divider/>
           <Tablerow main={false} num={props.working-1}/>
           <Tablerow main={true} num={props.working}/>
           <Tablerow main={false} num={props.working+1}/>
           <Divider/>
        </DialogContent>
        <DialogActions>
          <Button onClick={props.plusCount} variant="contained" color="primary" autoFocus>
            Next
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}

function Tablerow(props){
  const classes = useStyles();
  const {num,main} = props;

  return(
    <div style={{display:'flex',backgroundColor:main?"#3F51B5":"#fff"}}>
      <div className={classes.tablerow}>
          {main?<b className="idt">{num}</b>:<p>{num}</p>}
      </div>
      <div className={classes.tablerow}>
          {main?<b className="idt">{`#${num}`}</b>:<p>{`#${num}`}</p>}
      </div>
    </div>
  )
}