import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import InterruptRequest from './comp/interruptRequest'; 
import SelectInterrupt from './comp/selectIntterrupt';
import {ALL_STATE_VALS} from './_allStates';
import SetPriority from './comp/setPriority'
import SetMask from './comp/setMask'
import SettingMenu from './comp/SettingMenu';
import './App.css';

let width = window.innerWidth;
let height = window.innerHeight;
const priority_default=[
  {value:0},
  {value:1},
  {value:2},
  {value:3},
  {value:4},
  {value:5},
  {value:6},
  {value:7},
]

const useStyles = makeStyles({
  logo:{
    position:'absolute',
    display:'flex'
  },
  description:{
    width:width*0.8,
  },
  buttonGroup:{
    width:width*0.2,
    display:'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  footer:{
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    height:height*0.2
  },
  index:{
    marginRight:10,
    marginLeft:10
  }
});


function App() {
  const classes = useStyles();
  let [count, setCount] = React.useState(0);
  // μΈν„°λ½νΈ λ‹¨κ³„ μ¤‘ ν„μ¬ λ‡λ²μ§Έ λ‹¨κ³„μΈμ§€

  let [interrupt, setInterrupt] = React.useState([]); 
  let [interruptDialog,setInterruptDialog] = React.useState(false); 
  let [working, setWorking] = React.useState(-1); 

  let [mask, setMask] = React.useState([]); 
  let [MaskDialog, setMaskDialog] = React.useState(false); 

  let [priority,setPriority] = React.useState(priority_default); 
  let [PriorityDialog,setPriorityDialog] = React.useState(false); 

  
  const states =  ALL_STATE_VALS(working);  
  // κ° μΈν„°λ½νΈ λ‹¨κ³„λ³„ μ„¤λ…κ³Ό κµ¬μ„±μ”μ†λ¥Ό λ‚νƒ€λ‚΄λ” data

  const plusCount=()=>{ 
    if(count ===11){
      if(interrupt.length>0)setCount(2); //λ‚¨μ•„μλ” μΈν„°λ½νΈμ”μ²­μ„ μ²λ¦¬ν•©λ‹λ‹¤.
      setWorking(-1);
    }else{
      setCount(++count);
    }
  };//λ‹¤μλ‹¨κ³„λ΅ λ„μ–΄κ°€λ” ν΄λ¦­ν•¨μ μ…λ‹λ‹¤.

  const minusCount=()=>{
    if(count ===0) return;
    setCount(--count);
  };// μ „λ‹¨κ³„λ΅ λμ•„κ°€λ” ν΄λ¦­ν•¨μ μ…λ‹λ‹¤

    return (
      <div>
        <div className={classes.logo}>
          <SettingMenu 
            setMaskDialog={setMaskDialog}
            setPriorityDialog={setPriorityDialog}
            setInterruptDialog={setInterruptDialog}
          />
          <img src="title.png" alt="logo"/>
        </div>{/* λ©”λ‰΄ λ° λ΅κ³   */}
          
          <SelectInterrupt count={interruptDialog} interrupt={interrupt} setInterruptDialog={setInterruptDialog} mask={mask} setInterrupt={setInterrupt} plusCount={plusCount}/>
          <SetPriority priority={priority} setPriority={setPriority} PriorityDialog={PriorityDialog} setPriorityDialog={setPriorityDialog}/>
          <SetMask mask={mask} setMask={setMask} MaskDialog={MaskDialog} setMaskDialog={setMaskDialog}/>
           {/* Dialog for Setting */}

          <InterruptRequest
            priority={priority}
            working={working}
            setWorking={setWorking}
            mask={mask}
            interrupt={interrupt} 
            setInterrupt={setInterrupt} 
            minusCount={minusCount} 
            plusCount={plusCount} 
            count={count}
            setCount={setCount}
          />{/* ν„μ¬ μΈν„°λ½νΈ λ‹¨κ³„ μ‹κ°ν™”  */}
  
        <div className={classes.footer}> 
            <div className={classes.description} align="center">
              <p>{states[count].disc}</p>
              <h1>{states[count].title}</h1>
            </div>
            <div className={classes.buttonGroup}>
              <div onClick={minusCount}><img width={30} height={30} src={count===0?"prev2.png":"prev1.png"} alt="prev"/></div>
              <div className={classes.index}><h1>{states[count].id}</h1></div>
              <div onClick={plusCount}><img width={30} height={30}  src={count===states.length?"next2.png":"next1.png"} alt="next"/></div>
          </div>      
        </div>{/* λ§¨μ•„λ μ„μΉν• μΈν„°λ½νΈ μ„¤λ…λ¶€λ¶„ λ° λ„¤λΉ„κ²μ΄ν„° */}

      </div>
    )
  }


export default App;

